<template>
    <div>
        <div class="text mt32">
            <div class="text-size-xxl text-color-red text-bolder">
                Вы не можете написать отзыв на этот<br>
                купон, потому что вы его уже написали
            </div>

            <p class="mt24">
                Написать отзыв на купон можно только один раз<br>
                и редактировать его после уже нельзя.
            </p>
        </div>

        <div class="mt32">
            <span class="btn btn--white-red" @click="close">Теперь я тоже так считаю...</span>
        </div>

        <div class="mt32">
            P.S. При этом вы всегда можете <a @click="openCouponClaim" href="javascript:void(0)">пожаловаться на купон</a> руководителю проекта.
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'
    
    export default {
        name: "OpinionErrorSend",
        methods: {
            close() {
                this.$store.commit('popups/close', {name: this.popups.opinionErrorSend.name});
            },
            openCouponClaim: function () {
                this.$store.commit('popups/close', {name: this.popups.opinionErrorSend.name});
                this.$store.commit('popups/open', {name: this.popups.couponClaim.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

