<template>
    <div class="coupon my-coupon">
      <router-link @click.native="activeItem(item.action.id)"
                   :to="{ name: 'catalogDetail', params: { id: item.action.id }}"
                   class="coupon-href"></router-link>
      <div class="coupon-inner">
          <div class="coupon-img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.action.image+')' }">
              <div class="coupon-stat">
                  <div class="views stat-item">
                      <svg class="icon-views">
                          <use xlink:href="@/assets/img/mob-sprite.svg#views"></use>
                      </svg><span class="count">{{ item.action.showCounterFormat }}</span>
                  </div>
                  <div class="purchases stat-item" v-if="item.action.statistics.sold > 0">
                      <svg class="icon-cart">
                          <use xlink:href="@/assets/img/mob-sprite.svg#cart"></use>
                      </svg><span class="count">{{ item.action.statistics.sold }}</span>
                  </div>
                  <div class="sum stat-item" style="display: none">
                      <svg class="icon-coins">
                          <use xlink:href="@/assets/img/mob-sprite.svg#coins"></use>
                      </svg><span class="count">22 128 Р</span>
                  </div>
                  <!--<div class="comments stat-item" v-if="item.action.countOpinions > 0">
                      <svg class="icon-comment">
                          <use xlink:href="@/assets/img/mob-sprite.svg#comment"></use>
                      </svg><span class="count">{{  item.action.countOpinions }}</span>
                  </div>-->
              </div>
              <!--<div class="rating-stars">
                  <svg class="star" v-for="i in 5" :class="{active: i <= 3}">
                      <use xlink:href="@/assets/img/mob-sprite.svg#star"></use>
                  </svg>
              </div>-->
          </div>
          <div class="discount-flag" @click="openAverageCheck()">
            <span class="percent">−{{ item.action.discountFrom }}%</span>
            <span class="text-size-s text-normal sum">≈{{item.action.realSavingsFormat}} Р</span>
          </div>
          <div class="c-favourite" ref="favoriteBtn" @click="setFavorite(item)">
              <svg :class="{'icon-fav': true, 'fav': true, 'active': item.action.favorite}">
                  <use xlink:href="@/assets/img/mob-sprite.svg#fav"></use>
              </svg>

          </div>

          <div class="coupon-name">{{ item.action.name }}</div>
          <div class="top">
              <div class="company">{{ item.action.provider.name }}</div>
          </div>
        </div>
        <div class="separator-block">
          <div class="separator">
              <svg class="icon-separator">
                  <use xlink:href="@/assets/img/mob-sprite.svg#separator"></use>
              </svg>
              <svg class="icon-scissors">
                  <use xlink:href="@/assets/img/mob-sprite.svg#scissors"></use>
              </svg>
          </div>
        </div>
        <div class="status valid" v-if="item.status.code === 'valid'">
            <svg>
                <use xlink:href="@/assets/img/mob-sprite.svg#coupon-active"></use>
            </svg>
            Активен
        </div>
        <div class="status" v-if="item.status.code === 'extinguished'">
            <svg>
                <use xlink:href="@/assets/img/mob-sprite.svg#coupon-used"></use>
            </svg>
            Использован {{ item.status.date }}
        </div>
        <div class="status" v-if="item.status.code === 'expired'">Просрочен {{ item.status.date }}</div>
        <div class="status" v-if="item.status.code === 'canceled'">
            <svg>
                <use xlink:href="@/assets/img/mob-sprite.svg#coupon-deleted"></use>
            </svg>
            Аннулирован {{ item.status.date }}
        </div>
        <div class="status" v-if="item.status.code === 'withdrawn'">Отменён {{ item.status.date }}</div>
        <div class="sum-result" v-if="item.status.code === 'extinguished'"><span>Сумма покупки: <strong>{{item.priceFormat}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong> Скидка: <strong>{{item.discount.sumFormat}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong> ({{item.discount.percent}}%)</span></div>
        <ul class="actions">
            <li class="view-story"><a href="javascript:void(0);" @click="openCouponHistory">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#history"></use>
                </svg>
                <span>Посмотреть историю</span></a>
            </li>
            <!--<li class="send-sms" v-if="item.status.code === 'valid'"><a @click="openRepeatSendInfo" href="javascript:void(0);">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#send"></use>
                </svg>
                <span>Отправить на почту и по СМС</span></a>
            </li>-->
            <li class="complain"><a @click="openCouponClaim" href="javascript:void(0);">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#warning"></use>
                </svg>
                <span>Пожаловаться на купон</span></a>
            </li>
            <li class="complain" v-if="false && item.status.code === 'extinguished'"><a @click="openCouponOpinion" href="javascript:void(0);">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#write-review"></use>
                </svg>
                <span>Написать отзыв</span></a>
            </li>
            <!--<li class="refuse" v-if="item.status.code === 'valid'"><a @click="openCouponCancel" href="javascript:void(0);">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#c-remove"></use>
                </svg>
                <span>Отказаться от купона</span></a>
            </li>-->
            <li class="issue" v-if="item.status.code !== 'valid'">
                <router-link  @click.native="activeItem(item.action.id)"
                              :to="{ name: 'catalogDetail', params: { id: item.action.id }}"
                              v-if="+item.action.status === 19">
                    <svg class="icon">
                        <use xlink:href="@/assets/img/mob-sprite.svg#issue"></use>
                    </svg>
                    <span>Оформить ещё раз</span>
                </router-link>
                <a href="javascript:void(0);" @click="openCheckoutFail" v-else>
                    <svg class="icon">
                        <use xlink:href="@/assets/img/mob-sprite.svg#issue"></use>
                    </svg>
                    <span>Оформить ещё раз</span>
                </a>
            </li>
        </ul>
        <popup-info-block :show="popups.averageCheck.show"
               :add-class="''"
               :title="''"
               :modal-type="''"
               :text="''"
               :btntext="'Понятно'"
               :name="popups.averageCheck.name">
              <div class="modal-txt">
                <svg class="icon-certif">
                   <use xlink:href="@/assets/img/mob-sprite.svg#certif"></use>
                </svg>
                <p class="txt" style="font-size: 12px;line-height: 15px" >
                  При обращении к этому поставщику<br>средний чек составляет <strong>{{item.action.averageCheckFormat}} Р</strong>,<br>а экономия – около <strong>{{ item.action.realSavingsFormat}} Р</strong>
                </p>
              </div>
        </popup-info-block>
    </div>
    <!-- end coupon-stat -->
</template>

<script>
    import Popup from '@/components/modal/Popup.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';
    import {flyToFavorite} from '@/helpers/animate';
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "CatalogListItem",
        components: {
            Popup,
            Tooltip,
            PopupInfoBlock
        },
        props: {
            item: {
                default: function () {
                    return {}
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
        },
        methods: {
            openAverageCheck(){
              this.$store.commit('popups/open', {name: this.popups.averageCheck.name});
            },
            activeItem: function (id) {
                if(id){
                    this.$store.dispatch('actions/goCoupon', {id:id});
                }
            },
            setFavorite: function (item) {
                this.$store.dispatch('actions/setFavorite', item.action);
                if(item.action.favorite){
                    this.$store.commit('userCoupons/removeFavorite', item);
                } else {
                    this.$store.commit('userCoupons/addFavorite', item);
                }
            },
            openCouponCancel: function () {
                this.$store.commit('popups/open', {name: this.popups.couponCancel.name});
                this.$emit('current', this.item);
            },
            openCouponClaim: function () {
                this.$store.commit('popups/open', {name: this.popups.couponClaim.name});
                this.$emit('current', this.item);
            },
            openRepeatSendInfo: function () {
                this.$store.dispatch('userCoupons/resendCoupon', this.item);
                this.$store.commit('popups/open', {name: this.popups.repeatSendInfo.name});
                this.$emit('current', this.item);
            },
            openCheckoutFail: function () {
                this.$store.commit('popups/open', {name: this.popups.checkoutFail.name});
                this.$emit('current', this.item);
            },
            openCouponHistory: function () {
                this.$store.commit('popups/open', {name: this.popups.couponHistory.name});
                this.$emit('current', this.item);
            },
            openCouponOpinion: function () {
                let error = false;
                for (let opinion of this.user.opinions) {
                    if (+opinion.action === +this.item.action.id && +opinion.action > 0) {
                        error = true;
                    }
                }
                if(error){
                    this.$store.commit('popups/open', {name: this.popups.opinionErrorSend.name});
                    this.$emit('current', this.item);
                } else {
                    this.$store.commit('popups/open', {name: this.popups.opinionAdd.name});
                    this.$emit('current', this.item);
                }

            }
        }
    }

</script>

<style scoped>

</style>
