<template>
    <div class="coupon-list-inner inner">
        <popup class="refuse-coupon" :is-set-blank="true" :show="popups.couponCancel.show" :name="popups.couponCancel.name">
            <coupon-cancel :coupon="currentItem"></coupon-cancel>
        </popup>

        <popup class="refuse-coupon" :is-set-blank="true" :show="popups.couponClaim.show" :name="popups.couponClaim.name">
            <coupon-claim :action="currentItem.action"></coupon-claim>
        </popup>


        <popup-info-block :show="popups.repeatSendInfo.show"
               :is-set-blank="true"
               :add-class="''"
               :title="''"
               :modal-type="''"
               :text="''"
               :btntext="'Спасибо огромное!'"
               :name="popups.repeatSendInfo.name">
               <div class="big-ttl" >
                     <!-- Добавлять класс red или green к modal-ttl и modal-txt-->
                     <div class="modal-ttl">
                       <svg class="icon">
                         <use xlink:href="@/assets/img/mob-sprite.svg#like"></use>
                       </svg>
                     </div>
                     <div class="modal-txt">
                       <p>Информация о купоне повторно<br>отправлена на вашу почту и телефон</p>
                     </div>
               </div>
        </popup-info-block>

        <popup-info-block :show="popups.checkoutFail.show"
               :is-set-blank="true"
               :add-class="''"
               :title="''"
               :modal-type="''"
               :text="''"
               :btntext="'Очень жаль...'"
               :name="popups.checkoutFail.name">
               <div class="big-ttl modal-left">
                     <!-- Добавлять класс red или green к modal-ttl и modal-txt-->
                     <div class="modal-ttl red">Повторное оформление невозможно</div>
                     <div class="modal-txt">
                       <p>Данный купон находится в архиве<br>и не поддерживается поставщиком,<br>поэтому его повторное оформление<br>невозможно</p>
                     </div>
               </div>
        </popup-info-block>

        <popup :is-set-blank="true" :show="popups.couponHistory.show" :name="popups.couponHistory.name">
            <coupon-history class="coupon-history" :coupon="currentItem"></coupon-history>
        </popup>
        <popup
                :show="popups.opinionAdd.show"
                :is-set-blank="true"
                :name="popups.opinionAdd.name">
            <opinion-add :action="currentItem.action"></opinion-add>
        </popup>
        <popup-info-block
                :show="popups.opinionErrorSend.show"
                :is-set-blank="true"
                :modal-type="'isSend'"
                :name="popups.opinionErrorSend.name">
        </popup-info-block>
        <div class="list-name page-ttl">
            <div class="page-ttl__left">
                <span class="txt">Мои купоны</span>
            </div>
            <div class="page-ttl__right">
                <div class="open-user-info" @click="openUserInfo">
                    <svg class="icon-user">
                      <use xlink:href="@/assets/img/mob-sprite.svg#user"></use>
                    </svg>
                    <span class="text-bold text-color-yellow text-size-s">{{user.firstName}}</span>
                </div>
            </div>
        </div>
        <div class="coupon-list">
            <coupon-list-item v-for="coupon in coupons"
                               :item="coupon"
                              @current="setCurrent"
                               :key="'coupon'+coupon.id">
            </coupon-list-item>

            <loader v-if="loadAction" class=""></loader>


            <div class="no-favourites" v-else-if="isEmptyLoad">
              <svg class="icon-fav fav">
                <use xlink:href="@/assets/img/mob-sprite.svg#emoji-sleep"></use>
              </svg>
              <div class="nofav">У вас пока нет<br>оформленных купонов</div>
              <div class="addthem">Чтобы они были – добавьте их...</div>
              <div class="button-block icon-big">
                  <button class="btn" @click="goHome()">
                    <svg class="icon long-ticket">
                      <use xlink:href="@/assets/img/mob-sprite.svg#long-ticket"></use>
                    </svg>
                    <div class="txt">Смотреть скидки</div>
                  </button>
              </div>
            </div>
        </div>
        <popup-info-block :show="popups.userInfo.show"
               :add-class="'userInfo'"
               :title="''"
               :modal-type="''"
               :text="''"
               :btntext="'Понятно'"
               :name="popups.userInfo.name">
               <div class="user-info">
                 <div class="text">
                     <div class="text-bold text-size-m">
                         <svg class="icon-user">
                           <use xlink:href="@/assets/img/mob-sprite.svg#user"></use>
                         </svg>
                         {{user.firstName}}
                     </div>
                     <div class="text-size-m">
                         <svg class="icon-user">
                           <use xlink:href="@/assets/img/mob-sprite.svg#contact-info"></use>
                         </svg>
                         {{user.phone}}
                     </div>
                     <div class="text-size-m">
                         <svg class="icon-user">
                           <use xlink:href="@/assets/img/mob-sprite.svg#map-track"></use>
                         </svg>
                         {{currentCity.name}}
                     </div>
                 </div>
                 <div class="button-block">
                     <div class="exit modal-btn" @click="logout">
                         Выйти
                     </div>
                   <div class="close modal-btn" @click="closeUserInfo">
                       Отмена
                   </div>
                 </div>
               </div>
        </popup-info-block>
    </div>
    <!-- end coupon-list -->
</template>


<script>
    import { mapState } from 'vuex'
    import CouponListItem from '@/components/user/CouponListItem.vue'
    import Loader from '@/components/Loader.vue';
    import CouponCancel from '@/components/modal/CouponCancel.vue';
    import CouponClaim from '@/components/modal/CouponClaim.vue';
    import RepeatSendInfo from '@/components/modal/RepeatSendInfo.vue';
    import CheckoutFail from '@/components/modal/CheckoutFail.vue';
    import CouponHistory from '@/components/modal/CouponHistory.vue';
    import Popup from '@/components/modal/Popup.vue';
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';
    import {scrollTo} from '@/helpers/animate.js';
    import OpinionAdd from '@/components/modal/OpinionAdd.vue';
    import OpinionErrorSend from '@/components/modal/OpinionErrorSend.vue';

    export default {
        name: "CouponList",
        components: {
            CouponListItem,
            Loader,
            Popup,
            PopupInfoBlock,
            CouponCancel,
            CouponClaim,
            RepeatSendInfo,
            CheckoutFail,
            CouponHistory,
            OpinionAdd,
            OpinionErrorSend,
        },
        props: {
            favorites: {
                default: function () {
                    return false
                }
            }
        },
        created () {
            this.$store.dispatch('userCoupons/initialCouponState');
            this.$store.dispatch('userCoupons/getCouponList', {
                reload: true
            })
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            isEmptyLoad () {
                return !this.loadAction && !(this.coupons.length > 0);
            },
            ...mapState({
                loadAction: state => state.userCoupons.isLoad,
                currentCity:  state => state.cities.current,
                coupons:  state => state.userCoupons.coupons,
                favoritesItems: state => state.actions.favorites.items,
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
            opinionError() {
                if (this.user.id && this.currentItem.action) {
                    for (let opinion of this.user.opinions) {
                        if (+opinion.action === +this.currentItem.action.id && +opinion.action > 0) {
                            return 'isSend'
                        }
                    }
                    let isOrder = false;
                    for (let order of this.user.orders) {
                        if (+order.action === +this.currentItem.action.id && +order.action > 0) {
                            isOrder = true;
                        }
                    }
                    if (!isOrder) {
                        return 'notOrder'
                    }
                }
                return false
            },
        },
        data(){
            return {
                coords: [],
                currentItem: {},
            }
        },
        methods: {
            openUserInfo(){
                this.$store.commit('popups/open', {name: this.popups.userInfo.name});
            },
            closeUserInfo(){
                this.$store.commit('popups/close', {name: this.popups.userInfo.name});
            },
            goHome: function(){
              this.$router.push('/');
              this.$store.commit('actions/scrollToCatalogList', true);
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.getElementById('app').offsetHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('userCoupons/getCouponList', {currentSection: this.currentSection})
                }
            },
            setCurrent(item) {
                this.currentItem = item;
            },
            logout() {
                this.closeUserInfo();
                this.$store.dispatch('auth/logout');
                this.$router.push('/');
            },
        }

    }
</script>
