<template>
    <div>
      <div class="popup-header">
          <div class="popup-name">Написать отзыв на купон</div>
          <div class="close" @click="close()"><span class="i icon-close">
                        <svg class="icon">
                          <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                        </svg></span></div>
      </div>

        <div class="popup-content new-review">
          <div class="inner">
              <div class="text-bold text-center text-color mt24">
                  Общая оценка
              </div>
              <div class="rating-stars mt16">
                  <svg class="star" v-for="i in 5" :class="{'active': i <= currentIndex}" @click="currentIndex = i">
                      <use xlink:href="@/assets/img/mob-sprite.svg#rate-star"></use>
                  </svg>
              </div>
              <div class="text-size-s text-color-grey text-center mt16">
                  <span v-if="currentIndex == 1">Категорически не рекомендую!</span>
                  <span v-else-if="currentIndex == 2">Не рекомендую!</span>
                  <span v-else-if="currentIndex == 3">Фифти-фифти</span>
                  <span v-else-if="currentIndex == 4">Рекомендую</span>
                  <span v-else-if="currentIndex == 5">Рекомендую всем!</span>
                  <span v-else>Выберите от 1 до 5 звёзд</span>
              </div>

                <div class="text-bold text-center text-color mt24">
                    Достоинства
                </div>
                <div class="mt8">
                    <textarea-field  :placeholder="'Опишите, что именно вам понравилось '"
                                     v-model="text.value"
                                     :auto-focus="true"
                                     :is-error="text.error"
                                     @focus="removeError(text.name)"
                    ></textarea-field>
                </div>

                <div class="text-bold text-center text-color mt24">
                    Недостатки
                </div>
                <div class="mt8">
                    <textarea-field  :placeholder="'Опишите конкретные недостатки поставщика/продукции/услуги. Если их нет, то так и напишите.'"
                    ></textarea-field>
                </div>

                <div class="row-b mt32">
                    <div class="row-b__col elem-pos-fix mr24 button-block">
                        <button class="btn" @click="sendForm">
                            <span>Опубликовать</span>
                        </button>
                    </div>


                    <div class="underbtn">
                        Обратите внимание, оставить отзыв к купону можно<br>только один раз и изменить его после уже нельзя!
                    </div>
                </div>

          </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'

    export default {
        name: "OpinionAdd",
        components: {
            TextareaField
        },
        props: {
            action: {
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
                checkedPositive: true,
                checkedNegative: false,
                currentIndex: null,
                text: {
                    name: 'text',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: 'Опишите реальный опыт использования купона',
                },
            }
        },
        methods: {
            sendForm() {
                if(this.text.value.length < 10){
                    this.text.error = true;
                } else {
                    this.$store.dispatch('opinions/sendForm', {
                        text: this.text.value,
                        actionId: this.action.id,
                        type: this.checkedPositive ? 'positive' : 'negative'
                    });
                    this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
                    this.$store.commit('popups/open', {name: this.popups.opinionAddOk.name});
                }
            },
            removeError (field) {
                this[field].error = false;
            },
            close(){
                this.$store.commit('popups/close', {name: this.popups.opinionAdd.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

<style scoped>
  .new-review .switch-type .mt16{
    height: 200px !important;
  }
</style>
