<template>
    <div class="page-info-list profile">
        <div class="page-ttl">Личные данные</div>
        <ul class="info-list">
            <li>
                <div class="icon">
                    <svg class="icon-profile">
                        <use xlink:href="@/assets/img/mob-sprite.svg#profile"></use>
                    </svg>
                </div><span>{{ user.lastName }} {{ user.firstName }} {{ user.secondName }}</span>
            </li>
            <li>
                <div class="icon">
                    <svg class="icon-c-info">
                        <use xlink:href="@/assets/img/mob-sprite.svg#contact-info"></use>
                    </svg>
                </div><span>{{ user.phone }}</span>
            </li>
            <li>
                <div class="icon">
                    <svg class="icon-m-track">
                        <use xlink:href="@/assets/img/mob-sprite.svg#map-track"></use>
                    </svg>
                </div><span>{{ currentCity.name }}</span>
            </li>
        </ul>
        <div class="button-block">
            <button class="btn" @click="logout()">
                <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#exit"></use>
                </svg>
                <div class="txt">Выход</div>
            </button>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Popup from '@/components/modal/Popup.vue';

    export default {
        metaInfo() {
            return {
                title: 'Личные данные',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Personal",
        components: {
            Popup,
        },
        computed: {
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                currentCity:  state => state.cities.current,
                currentCityIp:  state => state.cities.currentCityIp,
            }),
        },
        mounted() {
            if(!this.token){
                this.$router.push('/');
            }
            ym(39511350, 'hit', this.$route.path, {title: 'Мои купоны | ZemSkidki.ru'});
        },
        methods: {
            logout() {
                this.$store.dispatch('auth/logout');
                this.$router.push('/');
            },
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
        }
    }
</script>
