<template>
    <div class="popup-info">
        <div>
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Ваш купон успешно оформлен!</div>

            <p class="mt24">
                Информация о порядке и сроках погашения<br>
                отправлена на вашу почту <strong>{{ user.email }}</strong>
            </p>
            
            <p>
                Сам купон находится в вашем личном кабинете.<br>
                Логин – ваш номер телефона, пароль отправлен вам по СМС.
            </p>
            
            <p>Спасибо, что работаете с нами!</p>
        </div>

        <div class="mt32">
            <button class="btn btn--white-green" @click="closeCouponBuyOk()">Вам тоже спасибо!</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "couponBuyOk",
        components: {
            Popup
        },
        methods: {
            closeCouponBuyOk: function () {
                this.$store.commit('popups/close', {name: this.popups.couponBuyOk.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
        }
    }
</script>

